"use client";

import { useRef } from "react";
import { useRouter } from "next/navigation";
import { encodeId } from "../../../lib/utils/secureId";

import {
  DATEICON,
  LOCATION_ICON,
  MODE_ICON,
  SAVEICON,
  TICKET_ICON,
  VIEW_ICON,
} from "../../../const-value/config-icons/page";

import "./EventSlider.css"; // CSS file

export default function EventSlider({ title, data = [], des }) {
  const router = useRouter();
  const sliderRef = useRef(null);

  console.log("check event", data);
  const slideLeft = () => {
    sliderRef.current?.scrollBy({ left: -350, behavior: "smooth" });
  };

  const slideRight = () => {
    sliderRef.current?.scrollBy({ left: 350, behavior: "smooth" });
  };

  const handleClick = (eventId) => {
    router.push(`/events/${encodeId(eventId)}`);
  };

  const formatDateTime = (iso) => {
    const d = new Date(iso);

    return {
      date: d.toLocaleDateString("en-IN", {
        day: "2-digit",
        month: "short",
        year: "numeric",
      }),
      time: d.toLocaleTimeString("en-IN", {
        hour: "2-digit",
        minute: "2-digit",
      }),
    };
  };

  return (
    <section className="container-fluid mt-4 px-5">
      {/* HEADER */}
      <div className="d-flex justify-content-between align-items-center mb-2">
        <div>
          <h5 className="fw-bold mb-0">{title}</h5>
          <p className="mt-4">{des}</p>
        </div>
        <button className="btn btn btn-outline-primary rounded-pill px-4">
          See all
        </button>
      </div>

      <hr />

      {/* NAV BUTTONS */}
      <div className="d-flex justify-content-end gap-2 mb-2">
        <button className="btn btn-light rounded-circle" onClick={slideLeft}>
          ‚ùÆ
        </button>
        <button className="btn btn-light rounded-circle" onClick={slideRight}>
          ‚ùØ
        </button>
      </div>

      {/* EMPTY */}
      {data.length === 0 ? (
        <p className="text-center text-muted py-4">No events found</p>
      ) : (
        <>
          <div
            className="d-flex gap-3 overflow-hidden"
            ref={sliderRef}
            style={{ scrollBehavior: "smooth" }}
          >
            {data.map((event, index) => {
              const { date, time } = formatDateTime(event.createdAt);

              return (
                <div
                  key={event.identity ?? index}
                  className="card event-card"
                  onClick={() => handleClick(event.identity)}
                >
                  {/* IMAGE */}
                  <img
                    src={
                      event.bannerImage ||
                      "https://cloudinary-marketing-res.cloudinary.com/images/w_1000,c_scale/v1679921049/Image_URL_header/Image_URL_header-png?_i=AA"
                    }
                    className="event-img"
                    alt="Event"
                  />

                  <div className="card-body p-3">
                    {/* TITLE */}
                    <div className="d-flex justify-content-between align-items-center mt-2">
                      <span className="fw-semibold ellipsis">
                        {event.title || "Untitled Event"}
                      </span>
                      {SAVEICON}
                    </div>

                    {/* DETAILS */}
                    <div className="text-sm text-muted mt-2">
                      <div className="d-flex justify-content-between">
                        <span className="ellipsis">
                          {LOCATION_ICON} {event.org?.city || "N/A"}
                        </span>

                        <span className="">{TICKET_ICON} 500</span>
                      </div>

                      <div className="d-flex justify-content-between mt-2">
                        <div>
                          <div className="event-date">
                            {DATEICON} {date} / {time}
                          </div>
                        </div>

                        <span
                          className={
                            event.mode === "online"
                              ? "mode-online"
                              : "mode-offline"
                          }
                        >
                          {MODE_ICON} {event.mode}
                        </span>
                      </div>
                    </div>

                    {/* FOOTER */}
                    <div className="d-flex justify-content-between align-items-center mt-3">
                      <span className="view-badge">{VIEW_ICON} 456</span>
                      <span className="badge-paid"> Paid</span>
                    </div>
                  </div>
                </div>
              );
            })}
          </div>
        </>
      )}
    </section>
  );
}
